:root {
  --center: 50%;
}

.stopwatch {
  border-radius: var(--center);
  border: 1vmin dotted #000;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes-hand {
  bottom: var(--center);
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
}

.stopwatch__seconds-hand {
  bottom: var(--center);
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
}

.stopwatch__center {
  border-radius: var(--center);
  top: var(--center);
  left: var(--center);
  background-color: #f6a603;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  transform: translate(-50%, -50%);
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation: 600s steps(60, end) infinite rotate;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation: 10s linear infinite rotate;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}
